import React, { useState } from 'react';
import SEO from '../components/shared/Seo';

import { Link } from 'react-router-dom';
import { AiFillFacebook } from 'react-icons/ai';
import styled from 'styled-components';

const LoginPage = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true);

    setTimeout(() => {
      setLoading(false);
    }, 3000);
  };

  const disabledButton = !email.trim() || !password.trim() || loading;

  return (
    <React.Fragment>
      <SEO title='Login' />
      <div className='container mx-auto pt-20 px-5'>
        <div className=' border border-gray-400 max-w-md mx-auto md:p-10 bg-white'>
          <img
            style={{ width: '200px' }}
            className='mb-5 mx-auto'
            src='https://upload.wikimedia.org/wikipedia/commons/thumb/2/2a/Instagram_logo.svg/1200px-Instagram_logo.svg.png'
            alt='Instagram'
          />
          <form onSubmit={handleSubmit} className='flex flex-col'>
            <input
              onChange={(e) => setEmail(e.target.value)}
              value={email}
              className='p-3 border rounded my-1 bg-gray-100 focus:outline-none'
              type='text'
              placeholder='Email'
            />
            <input
              onChange={(e) => setPassword(e.target.value)}
              value={password}
              className='p-3 border rounded my-1 bg-gray-100 focus:outline-none'
              type='Password'
              placeholder='Password'
            />
            {disabledButton ? (
              <button
                disabled={disabledButton}
                className='bg-blue-300 text-white p-2 rounded my-4 focus:outline-none cursor-not-allowed'
                type='submit'>
                {loading ? 'Loading...' : 'Log In'}
              </button>
            ) : (
              <button
                className='bg-blue-600 text-white p-2 rounded my-4 focus:outline-none'
                type='submit'>
                Log In
              </button>
            )}
          </form>
          <OrComponent />
          <LoginWithFacebook />
        </div>
        <Account />
      </div>
    </React.Fragment>
  );
};

export const OrComponent = () => {
  return (
    <div className='grid grid-cols-3'>
      <div className=' mt-2 border-t border-gray-500'></div>
      <h1 className='text-center text-gray-500'>OR</h1>
      <div className=' border-t mt-2 border-gray-500'></div>
    </div>
  );
};

const LoginWithFacebook = () => {
  return (
    <FacebookStyles>
      <div className='pt-4 text-center'>
        <Link
          className=' facebook flex justify-center items-center mb-4 font-medium'
          to='/'>
          <AiFillFacebook className='mr-2' size='1.5rem' /> Log in with Facebook
        </Link>
        <Link className='forgotten' to='/reset-password'>
          Forgot password?
        </Link>
      </div>
    </FacebookStyles>
  );
};

const Account = () => {
  return (
    <div className=' border border-gray-400 max-w-md mx-auto p-6 bg-white mt-3'>
      <div className='flex items-center justify-center'>
        <h1 className='mr-2 text-gray-600'>Don't have an account?</h1>
        <Link className='text-blue-600 font-medium' to='/accounts/emailsignup'>
          Sign up
        </Link>
      </div>
    </div>
  );
};

export default LoginPage;

const FacebookStyles = styled.div`
  .facebook {
    color: #385185;
  }
  .forgotten {
    color: #0092d1;
  }
`;
