import React, { useState } from 'react';
import { defaultCurrentUser } from '../../data';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { GearIcon, MoreIcon } from '../../icons';
import { FaUserAlt } from 'react-icons/fa';
import { AiFillCaretDown } from 'react-icons/ai';
import { BiUserCheck } from 'react-icons/bi';
import FollowButton from './FollowButton';
import ProfileOptionDialog from '../profile/ProfileOptionDialog';
import UsersOptionDialog from '../profile/UsersOptionDialog';

const ProfilePicture = ({
  isOwner = true,
  isFollower = true,
  following = true,
}) => {
  const [openOptionDialog, setOpenOptionDialog] = useState(false);
  const [usersOptionDialog, setUsersOptionDialog] = useState(false);

  // Closes Authenticated user option dialog
  const handleCloseOptionDialog = () => {
    setOpenOptionDialog(false);
  };

  // Opens authenticated user option dialog
  const handleOpenOptionDialog = () => {
    setOpenOptionDialog(true);
  };

  // Closes follower option dialog on the user account
  const handleCloseUserOptionDialog = () => {
    setUsersOptionDialog(false);
  };

  // Opens follower option dialog on the user account
  const handleOpenUsersOptionDialog = () => {
    setUsersOptionDialog(true);
  };
  return (
    <ProfilePictureStyles>
      <div className='hidden md:block mx-4'>
        <UserBioBigScreen
          handleOpenOptionDialog={handleOpenOptionDialog}
          handleOpenUsersOptionDialog={handleOpenUsersOptionDialog}
          isOwner={isOwner}
          isFollower={isFollower}
          following={following}
        />
      </div>
      <div className='mx-4 md:mx-auto md:hidden'>
        <UserBioSmallScreen
          handleOpenOptionDialog={handleOpenOptionDialog}
          handleOpenUsersOptionDialog={handleOpenUsersOptionDialog}
          isOwner={isOwner}
          isFollower={isFollower}
          following={following}
        />
      </div>
      {openOptionDialog && (
        <ProfileOptionDialog
          handleCloseOptionDialog={handleCloseOptionDialog}
        />
      )}
      {usersOptionDialog && (
        <UsersOptionDialog
          handleCloseUserOptionDialog={handleCloseUserOptionDialog}
        />
      )}
    </ProfilePictureStyles>
  );
};

const UserAvatar = ({
  size,
  image = 'https://scontent-lht6-1.cdninstagram.com/v/t51.2885-19/s320x320/35289006_189807175023620_1385861160439382016_n.jpg?_nc_ht=scontent-lht6-1.cdninstagram.com&_nc_ohc=i6KlgxnIjQAAX9eNQ9A&oh=c9646eabaa8b713f527a6e28915fa323&oe=5F88B4FA',
}) => {
  const { name } = defaultCurrentUser;

  return (
    <React.Fragment>
      <div>
        {image ? (
          <img
            className='rounded-full'
            style={{ width: size }}
            src={image}
            alt={name}
          />
        ) : (
          <FaUserAlt className='text-gray-600 mt-3' size='5rem' />
        )}
      </div>
    </React.Fragment>
  );
};

const UserBioBigScreen = ({
  isOwner,
  size = 200,
  isFollower,
  following,
  handleOpenOptionDialog,
  handleOpenUsersOptionDialog,
}) => {
  const { username, name } = defaultCurrentUser;

  return (
    <React.Fragment>
      <div className='grid grid-cols-3 mx-10 md:mx-auto'>
        <div className='col-span-1'>
          <UserAvatar size={size} />
        </div>
        <div className='col-span-2'>
          <div className='flex items-center'>
            <h1 className='text-3xl text-gray-600'>{username}</h1>
            {isOwner ? (
              <React.Fragment>
                <Link
                  className='border border-gray-500 py-1 px-2 focus:outline-none mx-4 font-medium rounded'
                  to='/accounts/edit'>
                  Edit Profile
                </Link>
                <button className='cursor-pointer focus:outline-none'>
                  <GearIcon onClick={handleOpenOptionDialog} />
                </button>
              </React.Fragment>
            ) : (
              <React.Fragment>
                {isFollower && following ? (
                  <>
                    <button className='bg-white font-medium py-1 px-8 text-gray-700 border border-gray-500 rounded mx-5 focus:outline-none'>
                      Message
                    </button>
                    <button className='border py-1 px-2 border-gray-500 rounded focus:outline-none'>
                      <BiUserCheck size='1.5rem' />
                    </button>
                  </>
                ) : (
                  <button className='bg-blue-600 py-1 px-5 mx-3 rounded text-white focus:outline-none'>
                    {isFollower && !following ? '  Follow back' : 'Follow'}
                  </button>
                )}
                <MoreIcon
                  onClick={handleOpenUsersOptionDialog}
                  className='ml-4 cursor-pointer'
                />
              </React.Fragment>
            )}
          </div>
          <PostCount user={defaultCurrentUser} />
          <h1 className='font-medium text-lg text-gray-800'>{name}</h1>
        </div>
      </div>
    </React.Fragment>
  );
};

const UserBioSmallScreen = ({
  isOwner,
  isFollower,
  following,
  handleOpenOptionDialog,
}) => {
  const { name, username } = defaultCurrentUser;
  return (
    <div className='text-gray-800'>
      <div className='grid grid-cols-3'>
        <div className='col-span-1'>
          <UserAvatar size={100} />
        </div>
        {isOwner ? (
          <React.Fragment>
            <div className='col-span-2'>
              <div className='flex items-center mb-5'>
                <h1 className='mr-4 text-2xl md:text-3xl text-gray-600'>
                  {username}
                </h1>
                <GearIcon
                  className='cursor-pointer'
                  onClick={handleOpenOptionDialog}
                />
              </div>
              <button className='border border-gray-500 py-1 w-full focus:outline-none rounded font-medium text-gray-800'>
                Edit Profile
              </button>
            </div>
          </React.Fragment>
        ) : (
          <React.Fragment>
            <div>
              <div className='flex items-center mb-4'>
                <h1 className='text-2xl'>{username}</h1>
                <button className='ml-3 cursor-pointer focus:outline-none mt-2'>
                  <MoreIcon />
                </button>
              </div>
              <div className='flex items-center'>
                {isFollower ? (
                  <Link
                    to='/direct/inbox'
                    className='bg-white border border-gray-500  font-medium py-1 px-5 text-gray-700 rounded mx-5 focus:outline-none'>
                    Message
                  </Link>
                ) : (
                  <div className='mx-3'>
                    <FollowButton />
                  </div>
                )}
                <button className=' text-gray-800 border border-gray-500 py-2 px-3 rounded focus:outline-none'>
                  <AiFillCaretDown />
                </button>
              </div>
            </div>
          </React.Fragment>
        )}
      </div>
      <div className='py-5 text-1xl font-medium'>
        <h1 className='text-gray-800'>{name}</h1>
      </div>
      <hr />
      <PostCount user={defaultCurrentUser} />
      <hr />
    </div>
  );
};

const PostCount = ({ user }) => {
  const options = ['posts', 'followers', 'following'];
  return (
    <div
      style={{ width: '25rem', maxWidth: '20rem' }}
      className='grid grid-cols-3 py-4 '>
      {options.map((option) => (
        <div key={option}>
          <div className='hidden md:block'>
            <div className='flex items-center text-base'>
              <p className='mr-2 font-semibold'>{user[option].length}</p>
              <p className='text-gray-800'>{option}</p>
            </div>
          </div>
          <div className='md:hidden'>
            <div className=' text-base text-center'>
              <p className='mr-2 font-semibold'>{user[option].length}</p>
              <p className='text-gray-800'>{option}</p>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default ProfilePicture;

const ProfilePictureStyles = styled.div``;
