import React, { useState, lazy, Suspense } from 'react';
import Layout from '../components/shared/Layout';
import SEO from '../components/shared/Seo';
import { getDefaultPost } from '../data';
import UserCard from '../components/shared/UserCard';
import FeedSideSuggestions from '../components/feed/FeedSideSuggestions';
import LoadingScreen, {
  LoadingBigIcon,
} from '../components/shared/LoadingScreen';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import FeedPostSkeleton from '../components/feed/FeedPostSkeleton';
const FeedPost = lazy(() => import('../components/feed/FeedPost'));

const FeedPage = ({ user }) => {
  const [loading] = useState(false);
  const [isEndofFeed] = useState(true);
  const avatar = 80;

  if (loading) {
    return <LoadingScreen />;
  }

  const settings = {
    swipeToSlide: true,
    arrows: true,
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 3,
    touchThreshold: 1000,
    responsive: [
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 4,
          initialSlide: 5,
          arrow: false,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 3,
          arrows: false,
        },
      },
    ],
  };

  return (
    <FeedPageStyles>
      {/* {modalPost && <PostModal hideModalPost={hideModalPost} />} */}
      <Layout>
        <SEO title='Feed' />
        <div className='grid grid-cols-3 gap-10 '>
          <div className='col-span-3 lg:col-span-2'>
            <Slider {...settings} className='border border-gray-500 p-5 mb-5 '>
              {Array.from({ length: 12 }, () => getDefaultPost()).map(
                (post) => (
                  <RecentVideos key={post.id} post={post} />
                )
              )}
            </Slider>
            {Array.from({ length: 5 }, () => getDefaultPost()).map(
              (post, index) => (
                <Suspense key={post.id} fallback={<FeedPostSkeleton />}>
                  <FeedPost post={post} index={index} />
                </Suspense>
              )
            )}
          </div>
          <div className='col-span-1 hidden lg:block '>
            <div className='fixed'>
              <UserCard user={user} avatar={avatar} />
              <FeedSideSuggestions />
            </div>
          </div>
        </div>
        {!isEndofFeed && <LoadingBigIcon />}
      </Layout>
    </FeedPageStyles>
  );
};

const FeedPageStyles = styled.div`
  .slick-next:before {
    color: rgba(38, 38, 38);
  }
  .slick-prev:before {
    color: rgba(38, 38, 38);
  }
  .slick-slider {
    z-index: 1;
  }
`;

const RecentVideos = ({ post }) => {
  const { user } = post;

  const textElipsis = (text) => {
    if (text?.length > 10) {
      return text.substring(0, 10) + '...';
    } else {
      return text;
    }
  };
  return (
    <UseRecenetVideoStyles>
      <Link to={`/${user.username}`}>
        <img
          className='w-16  mx-auto rounded-full mb-1 '
          src='https://www.biography.com/.image/t_share/MTU0MTU0MTc3NzkzMTA3MzU1/beyonce-attends-tidal-x-1015-on-october-15-2016-in-new-york-city-photo-by-theo-wargogetty-images-for-tidal-sqaure.jpg'
          alt={`${user.username}'s profile`}
        />
      </Link>
      <p className='text-center text-xs'>{textElipsis(user.username)}</p>
    </UseRecenetVideoStyles>
  );
};

const UseRecenetVideoStyles = styled.div`
  img {
    border: 2px solid #fe8347;
    padding: 2px;
  }
`;

export default FeedPage;
