import React, { useState } from 'react';
import styled from 'styled-components';
import UserCard from '../shared/UserCard';
import { MoreIcon } from '../../icons';
import {
  LikeIcon,
  ShareIcon,
  SaveIcon,
  CommentIcon,
  UnlikeIcon,
  RemoveIcon,
} from '../../icons';
import { Link } from 'react-router-dom';
import OptionsDialog from '../shared/OptionsDialog';
import { defaultPost } from '../../data';

const Post = () => {
  const [showOptionDialog, setDialog] = useState(false);
  const avatar = 45;
  const { id, likes, caption, comments, media, user } = defaultPost;

  return (
    <Styles>
      <div className='grid grid-cols-1 md:grid-cols-3 gap-2 border border-gray-500'>
        <div className='col-span-2'>
          <img className='post-image' src={media} alt='Post media' />
        </div>
        <div className='col-span-1 post-content'>
          <div className=' hidden md:flex justify-between items-center mb-3'>
            <div className='mt-1'>
              <UserCard user={user} avatar={avatar} />
            </div>
            <MoreIcon
              onClick={() => setDialog(true)}
              className='cursor-pointer mr-2'
            />
          </div>
          <hr />
          {/* postfeed details */}
          <div className='py-1 px-4'>
            <div className='post-text py-5'>
              <p
                className='mb-3 hidden md:block'
                dangerouslySetInnerHTML={{ __html: caption }}></p>
              {comments.map((content, index) => (
                <div className='mb-5 text-gray-700' key={index}>
                  <p dangerouslySetInnerHTML={{ __html: content }}></p>
                </div>
              ))}
            </div>
            <hr />
            <div className='relative  bottom-0'>
              <hr />
              <div className='grid grid-cols-12 pt-3'>
                <div className='col-span-11'>
                  <div className='flex items-center '>
                    <LikeButton />
                    <Link to={`/post/${id}`}>
                      <CommentIcon className='mx-2 cursor-pointer' />
                    </Link>
                    <ShareIcon className='mx-2 cursor-pointer' />
                  </div>
                </div>
                <SaveButton className='col-span-1' />
              </div>
              <div className='font-medium text-gray-600 mb-2 pt-3'>
                {likes > 1 ? `${likes} likes` : '1 like'}
              </div>
              <div className='text-gray-500 text-xs mb-4'>5 DAYS AGO</div>
              <hr />
              <Comment />
            </div>
          </div>
        </div>
      </div>
      {showOptionDialog && <OptionsDialog onClose={() => setDialog(false)} />}
    </Styles>
  );
};

// Like Button
const LikeButton = () => {
  const [liked, setLiked] = useState(false);
  return (
    <div
      onClick={() => setLiked((prevLike) => !prevLike)}
      className='cursor-pointer'>
      {liked ? <UnlikeIcon /> : <LikeIcon />}
    </div>
  );
};

// Save button
const SaveButton = () => {
  const [save, setSave] = useState(false);
  return (
    <div
      onClick={() => setSave((prevSave) => !prevSave)}
      className='cursor-pointer'>
      {save ? <RemoveIcon /> : <SaveIcon />}
    </div>
  );
};

// Comment component
const Comment = () => {
  const [content, setContent] = useState('');
  return (
    <div className='hidden md:block'>
      <form className=' flex items-center justify-between bg-white'>
        <input
          value={content}
          onChange={(e) => setContent(e.target.value)}
          className=' bg-none outline-none p-4'
          type='text'
          placeholder='Add a comment...'
        />
        <button
          disabled={!content.trim()}
          className={
            !content.trim()
              ? 'col-span-1 text-blue-200 cursor-not-allowed focus:outline-none '
              : 'col-span-1 text-blue-500 focus:outline-none'
          }>
          Post
        </button>
      </form>
    </div>
  );
};

const Styles = styled.div`
  .post-text {
    height: 25rem;
    overflow-y: auto;
    ::-webkit-scrollbar {
      display: none;
    }
  }

  .post-image {
    width: 100%;
    height: 40rem;
    object-fit: cover;
  }
`;

export default Post;
