import React, { useState, useEffect } from 'react';

import { Link } from 'react-router-dom';
import { AiFillFacebook } from 'react-icons/ai';
import styled from 'styled-components';
import { OrComponent } from './login';
import SEO from '../components/shared/Seo';

const SignUpPage = () => {
  const [disable, setDisable] = useState(false);
  const [input, setInput] = useState({
    email: '',
    fullName: '',
    username: '',
    password: '',
  });

  const handleChange = (e) => {
    setInput({
      ...input,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
  };

  useEffect(() => {
    if (
      input.email.length > 0 &&
      input.fullName.length > 0 &&
      input.username.length > 0 &&
      input.password.length > 0
    ) {
      setDisable(true);
    }
  }, [input]);

  return (
    <React.Fragment>
      <SEO title='Sign up' />
      <div className='container mx-auto pt-20 px-5'>
        <div className=' border border-gray-400 max-w-md mx-auto md:p-10 bg-white'>
          <img
            style={{ width: '200px' }}
            className='mb-5 mx-auto'
            src='https://upload.wikimedia.org/wikipedia/commons/thumb/2/2a/Instagram_logo.svg/1200px-Instagram_logo.svg.png'
            alt='Instagram'
          />
          <h1 className='font-medium md:text-2xl mb-3 text-gray-600'>
            Sign up to see photos and videos from your friends.
          </h1>
          <LoginWithFacebook />
          <OrComponent />
          <form onSubmit={handleSubmit} className='flex flex-col pt-4'>
            <input
              value={input.email}
              onChange={handleChange}
              name='email'
              className='p-3 border rounded my-1 bg-gray-100'
              type='text'
              placeholder='Email'
            />
            <input
              value={input.fullName}
              onChange={handleChange}
              name='fullName'
              className='p-3 border rounded my-1 bg-gray-100'
              type='text'
              placeholder='Full Name'
            />
            <input
              value={input.username}
              onChange={handleChange}
              name='username'
              className='p-3 border rounded my-1 bg-gray-100'
              type='text'
              placeholder='Username'
            />
            <input
              onChange={handleChange}
              value={input.password}
              name='password'
              className='p-3 border rounded my-1 bg-gray-100'
              type='Password'
              placeholder='Password'
            />
            <button
              disabled={!disable}
              style={{ background: !disable ? '#b3dffc' : '#0195f6' }}
              className=' text-white p-2 rounded my-4'
              type='submit'>
              Sign up
            </button>
            <h3 className='text-gray-500'>
              By signing up, you agree to our Terms, Data Policy and Cookies
              Policy
            </h3>
          </form>
        </div>
        <Account />
      </div>
    </React.Fragment>
  );
};

export const LoginWithFacebook = () => {
  return (
    <FacebookStyles>
      <Link
        className='text-white facebook flex justify-center items-center font-medium  w-full rounded p-3 mb-5'
        to='/'>
        <AiFillFacebook className='mr-2' size='1.5rem' /> Log in with Facebook
      </Link>
    </FacebookStyles>
  );
};

const Account = () => {
  return (
    <div className=' border border-gray-400 max-w-md mx-auto p-6 bg-white mt-3'>
      <div className='flex items-center justify-center'>
        <h1 className='mr-2 text-gray-600'> Have an account?</h1>
        <Link className='text-blue-600 font-medium' to='/accounts/login'>
          Log in
        </Link>
      </div>
    </div>
  );
};

export default SignUpPage;

// Styles

const FacebookStyles = styled.div`
  .forgotten {
    color: #0092d1;
  }
  a {
    background: #0195f6;
  }
`;
