import React, { useState } from 'react';
import Layout from '../components/shared/Layout';
import { defaultCurrentUser } from '../data';
import styled from 'styled-components';
import { useHistory } from 'react-router-dom';

const EditProfilePage = ({ user = defaultCurrentUser }) => {
  const history = useHistory();
  const path = history.location.pathname;
  const edit = Boolean(path.includes('edit'));
  const passwordChange = Boolean(path.includes('password'));
  return (
    <Layout>
      <EditProfileStyles>
        <div className=' edit-wrapper grid grid-cols-12 border border-gray-500 mx-4 md:mx-0 '>
          <div className=' hidden md:block col-span-3 border-r border-gray-500'>
            <SideNav edit={edit} passwordChange={passwordChange} />
          </div>
          <div className='col-span-9'>
            {edit && <UserDetails user={user} path={path} />}
            {passwordChange && <PasswordChange user={user} />}
          </div>
        </div>
      </EditProfileStyles>
    </Layout>
  );
};

const SideNav = () => {
  return (
    <React.Fragment>
      <GetTabs option='Edit Profile' pathName='/accounts/edit' />
      <GetTabs option='Change Password' pathName='/accounts/password/change' />
      <GetTabs option='Apps and Websites' pathName='/accounts/manage_access' />
    </React.Fragment>
  );
};

const GetTabs = ({ option, pathName }) => {
  const history = useHistory();

  const path = () => {
    return history.push(pathName);
  };

  const currentPath = Boolean(history.location.pathname === pathName);

  return (
    <button
      className={`flex w-full focus:outline-none py-3 px-4 hover:bg-gray-200 ${
        currentPath && 'border-l-2 border-black font-semibold bg-gray-200'
      }`}
      onClick={() => path(pathName)}>
      {option}
    </button>
  );
};

const UserFormItem = ({
  value,
  handleChange,
  name,
  description,
  isInput = true,
}) => {
  return (
    <div className='md:grid grid-cols-12'>
      <label className=' mb-2 col-span-2'>{description}</label>
      <div className='col-span-10'>
        {isInput ? (
          <input
            className='border border-gray-500 px-3 rounded mb-2 text-gray-800 py-1'
            onChange={handleChange}
            name={name}
            value={value}
            type='text'
          />
        ) : (
          <textarea
            className='border border-gray-500  px-3 rounded mb-3 text-gray-800'
            onChange={handleChange}
            name={name}
            value={value}
            type='text'>
            Bio
          </textarea>
        )}
      </div>
    </div>
  );
};

const UserDetails = ({ user, path }) => {
  const {
    username,
    name,
    profile_image,
    website,
    email,
    bio,
    phone_number,
    gender,
  } = user;
  const [file, setFile] = useState({});
  const [input, setInput] = useState({
    name,
    username,
    website,
    bio,
    email,
    phone_number,
    gender,
  });

  const handleFileUpload = (e) => {
    setFile(e.target.files[0]);
    console.log(file);
  };

  const handleChange = (e) => {
    setInput({
      ...input,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
  };

  return (
    <UserDetailStyle>
      <form onSubmit={handleSubmit} className='p-5'>
        <div className='flex items-center md:ml-20'>
          <img className='w-12 rounded-full' src={profile_image} alt='user' />
          <span className='ml-3 mt-5'>
            <p>{username}</p>
            <label>
              <p className='text-blue-600 cursor-pointer text-sm font-medium'>
                Change Profile Photo
              </p>
              <input
                onChange={handleFileUpload}
                className='file-selector'
                type='file'
              />
            </label>
          </span>
        </div>
        <div className='mb-4'>
          <UserFormItem
            isInput
            handleChange={handleChange}
            name='name'
            description='Name'
            value={input.name}
          />
          <small className='text-gray-600 block ml-0'>
            Help people discover your account by using the name you're known by:
            either your full name, nickname, or business name. You can only
            change your name twice within 14 days.
          </small>
        </div>
        <div className='mb-4'>
          <UserFormItem
            isInput
            handleChange={handleChange}
            name='username'
            description='Username'
            value={input.username}
          />
          <small className='text-gray-600 block ml-0'>
            In most cases, you'll be able to change your username back to
            i_am_samuelantwi for another 14 days
          </small>
        </div>
        <div className='mb-4'>
          <UserFormItem
            isInput
            handleChange={handleChange}
            name='website'
            description='Website'
            value={input.website}
          />
        </div>
        <UserFormItem
          isInput={false}
          handleChange={handleChange}
          name='bio'
          description='Bio'
          value={input.bio}
        />
        <div className='mb-4'>
          <div className='text-gray-600 block ml-0 personal-info mb-2'>
            <h1 className='mb-1'>Personal Information</h1>
            <p>
              Provide your personal information, even if the account is used for
              a business, a pet or something else. This won't be a part of your
              public profile.
            </p>
          </div>
          <UserFormItem
            handleChange={handleChange}
            name='email'
            description='Email'
            value={input.email}
          />
        </div>
        <div className='mb-4'>
          <UserFormItem
            handleChange={handleChange}
            name='phone_number'
            description='Phone Number'
            value={input.phone_number}
          />
        </div>
        <div className='mb-4'>
          <UserFormItem
            handleChange={handleChange}
            name='gender'
            description='Gender'
            value={input.gender}
          />
        </div>
        <div className='md:grid grid-cols-12 mb-4'>
          <label className='col-span-2'></label>
          <button
            type='submit'
            className='col-span-10 bg-blue-500 text-white rounded py-1 px-2 w-16'>
            Submit
          </button>
        </div>
      </form>
    </UserDetailStyle>
  );
};

// Password change component
const PasswordChange = ({ user }) => {
  const { profile_image, username } = user;
  const [oldPassword, setOldPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirm, setConfirm] = useState('');

  const disabled = Boolean(
    !oldPassword.trim() || !newPassword.trim() || !confirm.trim()
  );
  console.log(disabled);

  return (
    <PasswordChangeStyles>
      <div className='p-10'>
        <span className='flex items-center mb-5 md:ml-20'>
          <img
            className='w-10 rounded-full mr-4'
            src={profile_image}
            alt='user'
          />
          <h1 className='md:text-2xl text-gray-800'>{username}</h1>
        </span>
        <form action=''>
          <div className='md:grid grid-cols-12 mb-4'>
            <label className=' mb-2 col-span-3 font-semibold'>
              Old Password
            </label>
            <div className='col-span-9'>
              <input
                className='border border-gray-500 px-3 rounded mb-2 text-gray-800 py-1 bg-gray-200 focus:outline-none'
                onChange={(e) => setOldPassword(e.target.value)}
                value={oldPassword}
                type='password'
              />
            </div>
          </div>
          <div className='md:grid grid-cols-12 mb-4'>
            <label className=' mb-2 col-span-3 font-semibold'>
              New Password
            </label>
            <div className='col-span-9'>
              <input
                className='border border-gray-500 px-3 rounded mb-2 text-gray-800 py-1 bg-gray-200 focus:outline-none'
                onChange={(e) => setNewPassword(e.target.value)}
                value={newPassword}
                type='password'
              />
            </div>
          </div>
          <div className='md:grid grid-cols-12 mb-4'>
            <label className=' mb-2 col-span-3 font-semibold'>
              Confirm New Pasword
            </label>
            <div className='col-span-9'>
              <input
                className='border border-gray-500 px-3 rounded mb-2 text-gray-800 py-1 bg-gray-200 focus:outline-none'
                onChange={(e) => setConfirm(e.target.value)}
                value={confirm}
                type='password'
              />
            </div>
          </div>
          <div className='md:grid grid-cols-12 mb-4'>
            <label className='col-span-3'></label>
            <button
              disabled={disabled}
              type='submit'
              className={` ${!disabled && 'bg-blue-500 '} ${
                disabled && 'bg-blue-200 cursor-default'
              } col-span-9  text-white rounded py-1 px-2 `}>
              Change Password
            </button>
          </div>
        </form>
      </div>
    </PasswordChangeStyles>
  );
};

export default EditProfilePage;

// Components styles

const EditProfileStyles = styled.div`
  .edit-wrapper {
    height: 50rem;
  }
  @media (max-width: 768px) {
    .edit-wrapper {
      height: auto;
    }
  }
`;

const UserDetailStyle = styled.div`
  .file-selector {
    height: 0;
    width: 0;
    opacity: 0;
  }
  input,
  textarea {
    width: 20rem;
  }
  small {
    max-width: 20rem;
    font-size: 12px;
    margin-left: 6.5rem;
  }

  .personal-info {
    margin-left: 6.5rem;
    p {
      font-size: 12px;
    }
  }

  @media (max-width: 768px) {
    .personal-info,
    small {
      margin-left: 0px;
    }
  }
`;

const PasswordChangeStyles = styled.div`
  input {
    width: 25rem;
  }
  button {
    width: 10rem;
  }

  @media (max-width: 768px) {
    input {
      width: 17rem;
    }
  }
`;
