import React from 'react';
import { Link } from 'react-router-dom';
import { defaultUser } from '../../data';

function UserCard({ user = defaultUser, avatar }) {
  return (
    <React.Fragment>
      <div className='flex items-center'>
        <Link to={`/${user.username}`}>
          <img
            style={{ width: avatar }}
            className='  object-contain mr-4 rounded-full'
            src={user.profile_image}
            alt='User avatar'
          />
        </Link>
        <div>
          <Link
            className='block font-medium text-gray-800'
            to={`/${user.username}`}>
            <span className='flex items-center'>
              {user.username}{' '}
              {user.isStar && (
                <img
                  className='rounded-full w-4 ml-2'
                  src='https://s3-us-west-2.amazonaws.com/jumpermedia.co/uploads/2018/07/instagram-verified-symbol-768x768-1-300x300.png'
                  alt='star'
                />
              )}
            </span>
          </Link>
          <span className='block text-gray-500 text-sm'>{user.name}</span>
        </div>
      </div>
    </React.Fragment>
  );
}

export default UserCard;
